<template>
<div class="fade-in">
  <h1 class="page-header">Appointments</h1>
  <div id="newBtn_container">
    <router-link to="/"><i class="fas fa-home"></i></router-link>
    <button id="newBtn" @click="add = !add"><i class="fas fa-plus"></i></button>
  </div>
  <div id="createNewAppointment" v-if="add === true">
    <h5 class="new-heading">Enter new appointment details</h5>
    <form class="form-group col-md-12">
      <label for="name">Name of Appointment</label>
      <input type="text" name="name" class="form-control" v-model="appointment.name">
      <label for="app_date">Appointment Date</label>
      <input type="date" name="app_date" class="form-control" v-model="appointment.date">
      <label for="app_time">Appointment Time (optional)</label>
      <input type="time" name="app_time" class="form-control" v-model="appointment.time">
      <textarea type="text" class="form-control mb-2"  placeholder="description (optional)"  v-model="appointment.description"></textarea>
      <div id="btn-div" class="col-sm-12">
        <button class="btn btn-success col-sm-6 mt-2" @click.prevent="save_appointment">Save</button>
        <button class="btn btn-cancel col-sm-6 mt-2" @click.prevent="cancel_add_appointment">Cancel</button>
      </div>
    </form>
  </div>

  <!-- Display all appointments -->
  <div class="appointment-list" v-else>
    <ul style="width:100%;">
      <li v-for="appointment in sortedAppointments" :key="appointment.name" v-bind:class="{ expired: status(appointment.date) }">
        <div class="appointment-listitem scale-up-ver-top">
          <h4 class="appointment_name" style="color:red;">{{ appointment.name }}</h4>
          <p v-if="appointment.description">
            {{ appointment.description }}
          </p>
          <div class="app_date_container">
            <h5 class="appointment_date">On: {{ appointment.date }} </h5>
          </div>
          <div class="app_time_container" v-if="appointment.time">
            <h5 class="appointment_time">At: {{ appointment.time }}</h5>
          </div>
          <h5 class="appointment_status">status: <span style="color:red; font-style: italic;">{{ status(appointment.date) ? 'expired' : 'active' }}</span></h5>
          <label @click="remove_appointment(appointment.id)"><i class="fas fa-trash icon-trash"/></label>
        </div>
      </li>
    </ul>
  </div>

</div>
</template>

<script>
import { db } from "@/config/db.js";
import _ from "lodash";

export default {
  name: "appointments",
  data() {
    return {
      add: false,
      appointment: {
        userId: "",
        name: "",
        description: "",
        date: "",
        time: ""
      },
      appointments: null
    };
  },
  methods: {
    show_add() {
      this.add = true;
    },
    save_appointment() {
      if (this.appointment.name != "" && this.appointment.date != "") {
        if (
          !localStorage.getItem("myorganiser_userId") ||
          localStorage.getItem("myorganiser_userId") === ""
        ) {
          this.$router.push("/");
        }
        // Save appointment to firebase
        db
          .collection("Appointments")
          .add({
            user_id: localStorage.getItem("myorganiser_userId"),
            name:
              this.appointment.name.charAt(0).toUpperCase() +
              this.appointment.name.slice(1),
            date: this.appointment.date,
            description: this.appointment.description,
            status: "active",
            time: this.appointment.time
          })
          .then((data) => {
            this.appointments = data
            this.appointment.name = "";
            this.appointment.date = "";
            this.appointment.description = "";
            this.appointment.time = null;
            this.add = false;
          });
      }
    },
    cancel_add_appointment() {
      this.$router.push("/");
    },
    remove_appointment(id) {
      db
        .collection("Appointments")
        .doc(id)
        .delete();
    },
    status(d) {
      return new Date(d) < Date.now();
    }
  },
  firestore() {
    return {
      appointments: db
        .collection("Appointments")
        .where("user_id", "==", localStorage.getItem("myorganiser_userId"))
    };
  },
  created() {
    if (!localStorage.getItem("myorganiser_userId")) {
      this.$store.getters.getLoggedStatus === false;
      this.$router.push("/");
    }
  },
  computed: {
    sortedAppointments: function() {
      return _.orderBy(this.appointments, "date", "desc");
    }
  }
};
</script>

